* {
  box-sizing: border-box;
}

.side-drawer {
  height: 100%;
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 14%;
  min-width: 220px;
  max-width: 260px;
  z-index: 200;
  background-color: #fafafa;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
  box-sizing: border-box;
overflow: hidden;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 0;
}

.side-drawer li {
  margin: 0.3rem 0;
  width: 100%;
  padding: 5px 24px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 0;

}

.side-drawer a {
  color: #696969;
  text-decoration: none;
  font-size: 0.87rem;
}

a:hover, a:active, li:hover {
  background-color: #ededed;
  color: #000000;
}

.spacerr {
  flex: 1;
  border-top: 1px solid rgba(128, 128, 128, 0.342)
}

.spacerh {
  flex: 0.2;
  border-top: 1px solid rgba(128, 128, 128, 0.329)
}

.active{
  background-color: #d1d1d1;
}


.spacerr:hover {
  background-color: #fafafa;
}
@media (min-width: 1281px) {
  .side-drawer {
    /* display: none; */
    transform: translateX(0);
  }
}
@media (max-width: 1280px) {
  .side-drawer {
    /* display: none; */
    transform: translateX(-100%);
  }
}