.topbar {
    display: flex;
    justify-content: space-between;
    border-bottom: 0.5px solid rgba(214, 214, 214, 0.89);
    padding: 8px;
}

.typography, .c2 {
    font-size: 0.9375rem;
    font-family: -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    font-weight: 500;
    line-height: 1.4;
}

.blue-button {
    color: #fff;
    background-color: #28a3d7;
    padding: 8px 16px;
    box-shadow: none;
    font-size: 0.875rem;
    width: fit-content;
    min-width: 64px;
    box-sizing: border-box;
    font-family: -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    font-weight: 500;
    line-height: 1.75;
    white-space: nowrap;
    border-radius: 4px;
    text-transform: none;
    cursor: pointer;
}

.blue-button:hover {
    background-color: #3897c1;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba;
}

.c1 {
    color: rgba(0, 0, 0, 0.54);
    text-align: center;
    padding: 32px;
    box-sizing: inherit;
    font-size: 0.875rem;
    font-family: -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    font-weight: 400;
    line-height: 1.4;
    box-sizing: inherit;
}

.c2 {
    padding: 8px;
    border-bottom: 0.7px solid rgba(214, 214, 214, 0.95);
}
.c3 {
    
    border-bottom: 0.5px solid rgba(214, 214, 214, 0.89);
}
