.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #ffffff;
  height: 64px;

}
.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 24px;
  transition: transform 0.3s ease-out;

}

.toolbar__logo {
  margin-left: 1rem;
  transition: transform 0.3s ease-out;

}

.toolbar__logo a {
  color: rgb(97, 94, 94);
  text-decoration: none;
  font-size: 1.5rem;
  transition: transform 0.3s ease-out;

}
.spacer {
  flex: 1;
}
.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  transition: transform 0.3s ease-out;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
  transition: transform 0.3s ease-out;

}

.toolbar_navigation-items a {
  color: rgb(88, 88, 88);
  text-decoration: none;
  transition: transform 0.3s ease-out;

}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #fa923f;
}

@media (max-width: 500px) {
  .toolbar_navigation-items {
    transform: translateY(-200%);
  }
}

@media (min-width: 1280px) {
  .toolbar__toggle-button {
    transform: translateY(-200%);
  }

  .toolbar__logo {
    transform: translateY(-200%);
    margin-left: 0;
  }
}
