.workspace{

    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    border-radius: 8px;
}
.workspace:hover{
    background-color: #f5f5f5;
}

.svg-box {
    margin-right: 16px;
    padding: 10px;
}
.svg-box-2 {
    width: 48px;
    height: 48px;
    border-radius: 8px;
}
.svg-box-1 {
    display: flex;
    align-items: center;
    background-size: contain;
    justify-content: center;
    background-repeat: no-repeat;
    background-position: center center;
}

.workspace-title
{    margin-right: 16px;
    padding: 10px;
    font-size: 0.8125rem;
    font-family: -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    font-weight: 500;
    line-height: 1.4;
    padding-left: 0;
}