.App {
  text-align: center;
}
main{
  display: block;
  box-sizing: border-box;
  margin-top: 64px;
  margin-left:calc(220px + 3rem);
  margin-right:3rem;  
  padding:24px;
  transition: ease-in 0.3s;
  /* background-color: darkgrey; */
}
@media (max-width: 1280px) {
  main {
    margin-left:8%;
    padding-left: 16px;
    padding-right: 16px;
  }
}
@media (max-width: 970px) {
  main {
    margin-left:0;
    padding-left: 24px;
    padding-right: 24px;
  }
}